
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Box from 'vux/src/components/box/index.vue'


export default {
  directives: {
    TransferDom
  },
  components: {
    Group,
    Cell,
    Loading,
    XTextarea,
    XButton,
    Box
  },
  data () {
    return {
      show: false,
      status: false,
      assignment: {},
      id: 0
    }
  },
  methods: {
    complete: function (id) {
      this.$http(
        {
          method: 'get',
          url: '/public/wechat/completeAssignment?id=' + this.id
        }).then(function (response) {
          console.log(response)
          this.show = false
          this.status = true
        }, function (response) {
        })
    }
  },
  created () {
    this.id = this.$route.params.id
    console.log('crated')
    this.show = true
    this.$http(
      {
        method: 'get',
        url: 'http://192.168.1.103:8080/public/wechat/assignment?id=' + this.id
      }).then(function (response) {
        console.log(response)
        this.show = false
        this.assignment = response.data.data
        this.status = this.assignment.status
      }, function (response) {
      })
  }
}



