
import Divider from 'vux/src/components/divider/index.vue'
import Card from 'vux/src/components/card/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Panel from 'vux/src/components/panel/index.vue'
import Loading from 'vux/src/components/loading/index.vue'
import TransferDom from 'vux/src/directives/transfer-dom/index.js'


export default {
  directives: {
    TransferDom
  },
  components: {
    Card,
    Divider,
    Cell,
    Group,
    Panel,
    Loading
  },
  data () {
    return {
      show: false,
      type: '3',
      list: [{
        src: '/app/img/menu/self.svg',
        title: '基本信息',
        url: '/info'
      }, {
        src: '/app/img/menu/score.svg',
        title: '成绩查询',
        url: '/examlist'
      }, {
        src: '/app/img/menu/assignment.svg',
        title: '作业查询',
        url: '/assignmentList'
      }, {
        src: '/app/img/menu/notice.svg',
        title: '通知列表',
        url: '/informMessageList'
      }
      ],
      index: {}
    }
  },
  created () {
    console.log('crated')
    this.show = true
    this.$http(
      {
        method: 'get',
        url: '/public/wechat/index'
      }).then(function (response) {
        console.log(response)
        this.show = false
        this.index = response.data.data
      }, function (response) {
      })
  }
}



